'use strict';

import Gantt from './core';

const defaultOptions = {
    linkOutlineWidth : 2
};

const ID_DELIM = '_';
const TOOLTIP_FADING_TIME = 1000;
const TOOLTIP_SHOWING_DELAY = 800;

const CONSTRAINT_LINK_CLASS = 'constraint-link';
const CONSTRAINT_LINK_CTNR_CLASS = 'constraint-link-ctnr';
const CONSTRAINT_ARROW = 'constraint-arrow';

const SELECTION_CLASS = 'selected';

export default class ConstraintsGraph extends Gantt.components.ConstraintsGraph {
    constructor(gantt, node, config) {
        super(gantt, node, Gantt.utils.mergeObjects({}, defaultOptions, config));
    }

    setConfiguration(config) {
        this.destroy();

        this.create();
        this.ready = Promise.resolve([]);

        if (config.layout && Gantt.utils.isFunction(config.layout)) {
            this.layout = new config.layout(this.gantt);
        }
        else {
            this.layout = new (Gantt.components.ConstraintLayout.impl || Gantt.components.ConstraintLayout)(this.gantt, config.layout);
        }

        this.linkRenderer = new (Gantt.components.Renderer.impl || Gantt.components.Renderer)(config.renderer, LinkRendererPrototype, this.gantt);
        if (config.linkOutlineWidth)
            this.linkRenderer.linkOutlineWidth = config.linkOutlineWidth;
        // Ugly way for managing the exception of this view having several DOM nodes in its life
        const node = this.node;
        this.node = null;
        this.setNode(node);

        const selectionHandler = this.gantt.selection;
        selectionHandler.on(Gantt.events.CONSTRAINT_SELECTION_CLEARED, (e, sels) => this.clearConstraintSelection(sels));
        selectionHandler.on(Gantt.events.CONSTRAINT_SELECTED, (e, sels) =>  this.selectConstraints(sels));
        selectionHandler.on(Gantt.events.CONSTRAINT_UNSELECTED, (e, sels) =>  this.unselectConstraints(sels));
    }

    setNode(node) {
        if (this.node) {
            this.gantt.errorHandler.removeGroup(this.node);
            if (this.tooltipEnter) {
                Gantt.utils.removeEventListener(this.node, 'mouseenter', this.tooltipEnter);
                Gantt.utils.removeEventListener(this.node, 'mouseleave', this.tooltipLeave);
                Gantt.utils.removeEventListener(this.node, 'click', this.clickHandler);
            }
            this.node.innerHTML = '';
        }
        this.node = node;
        if (node) {
            if (!this.tooltipEnter) {
                this.tooltipEnter = (evt) => {
                    const node = this.getConstraintNode(evt.target);
                    if (node && (!this.gantt.timeTable.isDragAndDropping || !this.gantt.timeTable.isDragAndDropping())) {
                        this.showTooltip(node);
                    }
                };
                this.tooltipLeave = (evt) => {
                    const node = this.getConstraintNode(evt.target);
                    if (node && (this.tooltipElt == node)) {
                        this.hideTooltip(TOOLTIP_FADING_TIME);
                    }
                };
                this.clickHandler = (e) => this.processClick(e);
            }
            Gantt.utils.addEventListener(this.node, 'mouseenter', this.tooltipEnter, true);
            Gantt.utils.addEventListener(this.node, 'mouseleave', this.tooltipLeave, true);
            Gantt.utils.addEventListener(this.node, 'click', this.clickHandler, true);
        }
    }

    create() {
        if (this.config && this.config.classes) {
            Gantt.utils.addClass(this.config.classes);
        }
    }

    setConstraints(cts) {
        this.cts = cts;
        if (cts && cts.length) {
            this.ready = new Promise((resolve) => {
                setTimeout(()=> {
                    this.processConstraints(cts);
                    resolve();
                }, 0);
            });
        }
        else {
            this.ready = Promise.resolve([]);
        }
    }

    processConstraints(cts) {
        const table = this.gantt.table;
        const activityFilter = this.gantt.timeTable.getActivityFilter();
        let row, count, acts, act, i;
        this.layout.startInitialize();
        try {
            for (row = table.getFirstVisibleRow(); row; row = table.nextRow(row)) {
                acts = row.activities;
                count = acts && acts.length;
                if (count) {
                    for (i = 0; i < count; ++i) {
                        act = acts[i];
                        act = (!activityFilter || activityFilter.accept(act, row)) ? act : null;
                        if (act) {
                            act.consNode = this.layout.addNode(act, row.index);
                        }
                    }
                }
            }

            let from, to, cons;
            for (i = 0, count = cts.length; i < count; ++i) {
                cons = cts[i];
                cons.nodes = null; // Remove nodes from previous display
                from = cons.from.consNode;
                to = cons.to.consNode;
                if (from && to) {
                    this.layout.addConstraint(from, to, cons);
                }
            }
        } catch (e) {
            this.addError(e, "Error processing constraints");
            this.layout.stopInitialize();
            throw e;
        }

        this.layout.stopInitialize();
    }

    addError(e, msg) {
        if (!this.errorNode) {
            this.errorNode = document.createElement('div');
            this.errorNode.style.opacity = '0.80';
            this.gantt.timeTablePanel.appendChild(this.errorNode);
        }
        this.gantt.errorHandler.addError(e, msg, this.errorNode);
    }

    destroy() {
        if (this.layout && this.layout.destroy) {
            this.layout.destroy();
        }
        this.layout = null;
        if (this.node && this.tooltipEnter) {
            Gantt.utils.removeEventListener(this.node, 'mouseenter', this.tooltipEnter);
            Gantt.utils.removeEventListener(this.node, 'mouseleave', this.tooltipLeave);
            this.tooltipEnter = this.tooltipLeave = null;
        }
    }

    clearCache() {
        if (this.cts) {
            for (let i = 0, count = this.cts.length; i < count; ++i) {
                this.cts[i].fromNode = null;
                this.cts[i].toNode = null;
            }
        }
    }

    clear() {
        this.node.innerHTML = '';
        this.clearCache();
    }
    
    clearLinks() {
        this.layout.clearLinks();
    }

    resetLayout() {
        this.layout.resetLayout();
    }

    draw(rows, drawCB) {
        if (!rows.length)  return this.ready;
        return this.ready.then(()=> {
            const table = this.gantt.table;
            let firstRowIndex = rows[0].row.index, lastRowIndex = rows[rows.length - 1].row.index;

            function getNodeRect(act, index) {
                let row, y;
                if (index < firstRowIndex) {
                    row = rows[0].row;
                    y = rows[0].y;
                    while(index < firstRowIndex) {
                        row = table.prevRow(row);
                        drawCB(row);
                        y-= row.activityRow.height;
                        rows.splice(0, 0, { y : y, row : row, height : row.activityRow.height, index: --firstRowIndex });
                    }
                }
                else if (index > lastRowIndex) {
                    row = rows[lastRowIndex];
                    y = row.y + row.height;
                    row = row.row;
                    while(index > lastRowIndex) {
                        row = table.nextRow(row);
                        drawCB(row);
                        rows.push({ y : y, row : row, height : row.activityRow.height, index : ++lastRowIndex });
                        y+= row.activityRow.height;
                    }
                }
                row = rows[index - firstRowIndex];
                return act.node?
                { x : act.left, y : row.y, top : parseInt(act.node.style.top),
                    width: act.node.offsetWidth, height: act.node.offsetHeight, rowHeight : row.height }
                    : { x: 0, y : row.y, top : 0, width: 0, height: 0, rowHeight: row.height };
            }

            this.layout.getNodeRect = getNodeRect;
            const ctx = { gantt: this.gantt,
                nodeToLinkPadding : this.layout.horizLinkToNodeDist,
                linkToLinkPadding : this.layout.horizLinkToLinkDist };

            let i, rowIndex;
            const count = rows.length;

            try {
                for (i = 0, rowIndex = firstRowIndex; i < count; i++, rowIndex++) {
                    this.layout.layoutRowNodeLinks(rowIndex, this.linkRenderer, ctx);
                }
            } catch (e) {
                this.addError(e);
            }

            try {
                const fragment = document.createDocumentFragment();
                for (i = 0, rowIndex = firstRowIndex; i < count; i++, rowIndex++) {
                    this.layout.drawRowLinks(rowIndex, fragment, this.linkRenderer, ctx);
                }

                this.node.appendChild(fragment);
            } catch (e) {
                this.addError(e, "Error drawing constraints");
            }
        });
    }
    
    getConstraint(node) {
        let id = node.id;
        let lastIndex = id.indexOf(ID_DELIM), index;
        if (lastIndex >= 0) {
            while((index = id.indexOf(ID_DELIM, lastIndex + 1)) > 0) {
                lastIndex = index;
            }
            id = id.substring(0, lastIndex);
        }
        let cons;
        if (this.cts.byIds && (cons = this.cts.byIds[id])) {
            return cons;
        }
        return null;
    }

    getConstraintNode(elt) {
        let node = null;
        for(;elt && (elt !== this.node); elt = elt.parentNode) {
            if (Gantt.utils.hasClass(elt, CONSTRAINT_LINK_CTNR_CLASS)
                    || Gantt.utils.hasClass(elt, CONSTRAINT_ARROW)) return elt;
            if (Gantt.utils.hasClass(elt, CONSTRAINT_LINK_CLASS)) node = elt;
        }
        return node;
    }

    /*                 */
    /*     Tooltips    */
    /*                 */
    showTooltip(consNode) {
        let cons;
        if (consNode && (consNode != this.tooltipElt) && (cons = this.getConstraint(consNode))) {
            this.tooltipElt = consNode;
            const ctx = {
                limitElt: this.gantt.getBody(),
                showDelay : TOOLTIP_SHOWING_DELAY
            };
            this.gantt.tooltip.showTooltip(consNode, ctx,(parent) => {
                const ctx = { gantt: this.gantt };
                this.linkRenderer.getTooltip(parent, cons, ctx);
            });
        }
    };

    hideTooltip(fadingTime) {
        this.tooltipElt = null;
        this.gantt.tooltip.hideTooltip(fadingTime);
    }

    /**
     * Selection management
     */
    processClick(e) {
        this.hideTooltip();
        const consNode = this.getConstraintNode(e.target);
        let cons;
        if (consNode && (cons = this.getConstraint(consNode))) {
            this.gantt.selection.processClick(e, cons);
        }
    }

    //noinspection JSMethodCanBeStatic
    clearConstraintSelection(sels) {
        for(let i = 0, nodes, count = sels.length, sel; i < count; ++i) {
            sel = sels[i];
            if (nodes = sel.nodes) {
                for(let j = 0, count = nodes.length; j < count; j++) {
                    Gantt.utils.removeClass(nodes[j], SELECTION_CLASS);
                }
            }
        }
    }

    //noinspection JSMethodCanBeStatic
    selectConstraints(sels) {
        for(let i = 0, nodes; i < sels.length; i++) {
            if (nodes = sels[i].nodes) {
                for(let j = 0, count = nodes.length; j < count; j++) {
                    Gantt.utils.addClass(nodes[j], SELECTION_CLASS);
                }
            }
        }
    }

    //noinspection JSMethodCanBeStatic
    unselectConstraints(sels) {
        for(let i = 0, nodes; i < sels.length; i++) {
            if (nodes = sels[i].nodes) {
                for(let j = 0, count = nodes.length; j < count; j++) {
                    Gantt.utils.removeClass(nodes[j], SELECTION_CLASS);
                }
            }
        }
    }
};

const STYLE_ARROW = 1;

class NodeLabelLayout {
    constructor() {
    }

    startLayout(act, ctx) {}
    getNodeToLabelSpacing(act, bbox, textWidth, ctx) {}
}

class DefaultNodeLabelLayout extends NodeLabelLayout {
    constructor(left) {
        super();
        this.left = left;
    }

    getNodeToLabelSpacing(act, bbox, textWidth, ctx) {
        return this.left? -ctx.nodeToLinkPadding : ctx.nodeToLinkPadding;
    }
}

const LinkRendererPrototype = {
    drawLink(object, points, style, parentElt, ctx) {
        if (points.length < 2) return null;
        let lastX = points[0].x, lastY = points[0].y;
        const nodes = [];
        const color = this.color? this.color(object, ctx) : undefined;
        var css = this.getCSS && this.getCSS(object, ctx);
        const w = this.lineWidth && this.lineWidth(object, ctx);
        let i = 0, line, pt, horiz;
        while (true) {
            line = document.createElement('div');
            line.style.position = 'absolute';
            line.style.pointerEvents = 'auto';
            pt = points[++i];
            if (object && object.id) line.id = object.id + ID_DELIM + i;
            horiz = pt.y === lastY;
            line.className = CONSTRAINT_LINK_CLASS + ' ' + (horiz? 'constraint-horiz-link' : 'constraint-vert-link');

            if (this.linkOutlineWidth) {
                line.className = CONSTRAINT_LINK_CTNR_CLASS + ' ' + (horiz? 'constraint-horiz-link' : 'constraint-vert-link');
                if (horiz) {
                    line.style.height = ((w || 1) + this.linkOutlineWidth + this.linkOutlineWidth) + 'px';
                    line.style.paddingTop = line.style.paddingBottom = this.linkOutlineWidth + 'px';
                    line.style.top = (lastY - this.linkOutlineWidth) + 'px';
                    line.style.left = Math.min(lastX, pt.x) + 'px';
                    line.style.width = (Math.abs(lastX - pt.x) + ((i && lastX > pt.x)? (w || 1) : 0)) + 'px';
                    if (css) line.className+= ' ' + css;

                    const link = document.createElement('div');
                    link.className = CONSTRAINT_LINK_CLASS + ' constraint-horiz-link';
                    if (w) link.style.height = w + 'px';
                    link.style.width = '100%';
                    line.appendChild(link);
                }
                else {
                    line.style.width = ((w || 1) + this.linkOutlineWidth + this.linkOutlineWidth) + 'px';
                    line.style.paddingLeft = line.style.paddingRight = this.linkOutlineWidth + 'px';
                    line.style.left = (lastX - this.linkOutlineWidth) + 'px';
                    line.style.top = Math.min(lastY, pt.y) + 'px';
                    line.style.height = Math.abs(lastY - pt.y) + 'px';
                    if (css) line.className+= ' ' + css;

                    const link = document.createElement('div');
                    link.className = CONSTRAINT_LINK_CLASS + ' constraint-vert-link';
                    if (w) link.style.width = w + 'px';
                    link.style.height = '100%';
                    line.appendChild(link);
                }
            }
            else if (horiz) {
                if (w) line.style.height = w + 'px';
                line.style.top = lastY + 'px';
                line.style.left = Math.min(lastX, pt.x) + 'px';
                line.style.width = (Math.abs(lastX - pt.x) + ((i && lastX > pt.x)? (w || 1) : 0)) + 'px';
                if (css) line.className+= ' ' + css;
            }
            else {
                if (w) line.style.width = w + 'px';
                line.style.left = lastX + 'px';
                line.style.top = Math.min(lastY, pt.y) + 'px';
                line.style.height = Math.abs(lastY - pt.y) + 'px';
                if (css) line.className+= ' ' + css;
            }
            nodes.push(line);
            parentElt.appendChild(line);
            if (i === (points.length - 1)) {
                if (style === STYLE_ARROW) {
                    if (horiz) {
                        const arrowWidth = this.arrowWidth(object, ctx), arrowHeight = this.arrowHeight(object, ctx);
                        if (lastX < pt.x) {
                            line = this.drawRightArrow(pt.x, lastY, arrowWidth, arrowHeight, color);
                        }
                        else {
                            line = this.drawLeftArrow(pt.x, lastY, arrowWidth, arrowHeight, color);
                        }
                        line.style.pointerEvents = 'auto';
                        if (css) line.className+= ' ' + css;
                        if (object && object.id) line.id = object.id + ID_DELIM + 'arrow';
                        nodes.push(line);
                        parentElt.appendChild(line);
                    }
                }
                return nodes;
            }
            else {
                lastX = pt.x;
                lastY = pt.y;
            }
        }
    },

    drawLeftArrow(x, y, arrowWidth, arrowHeight, color) {
        const node = document.createElement('div');
        node.style.position = 'absolute';
        node.className = CONSTRAINT_ARROW + ' constraint-left-arrow';
        node.style.left = x + 'px';
        node.style.top = (y - (arrowHeight - 1))+ 'px';
        node.style.width = '0';
        node.style.height = '0';
        node.style.borderTop = arrowHeight + 'px solid transparent';
        node.style.borderBottom = arrowHeight + 'px solid transparent';
        node.style.borderRightWidth = arrowWidth + 'px';
        node.style.borderRightStyle = 'solid';
        if (color) node.style.borderRightColor = color;
        return node;
    },

    drawRightArrow(x, y, arrowWidth, arrowHeight, color) {
        const node = document.createElement('div');
        node.style.position = 'absolute';
        node.className = CONSTRAINT_ARROW + ' constraint-right-arrow';
        node.style.left = (x - arrowWidth) + 'px';
        node.style.top = (y - (arrowHeight - 1))+ 'px';
        node.style.width = '0';
        node.style.height = '0';
        node.style.borderTop = arrowHeight + 'px solid transparent';
        node.style.borderBottom = arrowHeight + 'px solid transparent';
        node.style.borderLeftWidth = arrowWidth + 'px';
        node.style.borderLeftStyle = 'solid';
        if (color) node.style.borderLeftColor = color;
        return node;
    },

    arrowWidth() {
        return 5;
    },

    arrowHeight() {
        return 5;
    },

    getTooltipProperties : function(cons, ctx) {
        const getName = function(act) {
            return act.name || act.id;
        };
        const props = [ Gantt.utils.getString('gantt.constraintChart.from'), getName(cons.from),
            Gantt.utils.getString('gantt.constraintChart.to'), getName(cons.to), Gantt.utils.getString('gantt.constraintChart.type')];
        let type;
        if (cons.type === Gantt.constraintTypes.START_TO_START) type = Gantt.utils.getString('gantt.constraintChart.start.start');
        else if (cons.type === Gantt.constraintTypes.START_TO_END) type = Gantt.utils.getString('gantt.constraintChart.start.end');
        else if (cons.type === Gantt.constraintTypes.END_TO_END) type = Gantt.utils.getString('gantt.constraintChart.end.end');
        else type = Gantt.utils.getString('gantt.constraintChart.end.start');
        props.push(type);
        return props;
    },

    processConfiguration : function(selector, config) {
        this.defaultProcessConfiguration(selector, config);

        if (config.lineWidth) {
            this.addLineWidthConfiguration(selector, config.lineWidth);
        }

        if (config.arrowWidth) {
            this.addArrowConfiguration(selector, config.arrowWidth, 'arrowWidth');
        }

        if (config.arrowHeight) {
            this.addArrowConfiguration(selector, config.arrowWidth, 'arrowHeight');
        }

        if (config.nodeLabelLayout) {
            this.addNodeLabelLayoutConfiguration(selector, config.nodeLabelLayout);
        }
    },

    setColor(elt, color) {
        elt.style.color = color;
    },

    destroy() {
    },

    addLineWidthConfiguration(selector, config) {
        var lineWidth;
        if (typeof config == "function") {
            lineWidth = config;
        }
        else if (typeof config == "string") {
            const getter = Gantt.utils.propertyEvaluator(config);
            if (getter) {
                lineWidth = (object, ctx) => {
                    return getter(object, ctx);
                }
            }
        }
        else {
            lineWidth = () => {
                return config;
            }
        }
        if (lineWidth) {
            const oldLineWidth = this.lineWidth;
            this.lineWidth = (object, ctx) => {
                if (!selector || selector(object, ctx)) {
                    return lineWidth(object, ctx);
                }
                return oldLineWidth && oldLineWidth(object, ctx);
            };
        }
    },

    addArrowConfiguration(selector, config, field) {
        var arrowSize;
        if (typeof config == "function") {
            arrowSize = config;
        }
        else if (typeof config == "string") {
            const getter = Gantt.utils.propertyEvaluator(config);
            if (getter) {
                arrowSize = (object, ctx) => {
                    return getter(object, ctx);
                }
            }
        }
        else {
            arrowSize = () => {
                return config;
            }
        }
        if (arrowSize) {
            const oldArrowSize = this[field];
            this[field] = (object, ctx) => {
                if (!selector || selector(object, ctx)) {
                    return arrowSize(object, ctx);
                }
                return oldArrowSize && oldArrowSize(object, ctx);
            };
        }
    },

    addNodeLabelLayoutConfiguration(selector, config) {
        let labelLayout;
        if (typeof config == "string") {
            if ((config === 'left') || (config === 'LEFT')) {
                labelLayout = new DefaultNodeLabelLayout(true);
            }
            else if ((config === 'right') || (config === 'RIGHT')) {
                labelLayout = new DefaultNodeLabelLayout(false);
            }
        }
        else if (Gantt.utils.isFunction(config)) {
            labelLayout = new NodeLabelLayout();
            labelLayout.getNodeToLabelSpacing = config;
        }
        else {
            labelLayout = config;
        }
        if (labelLayout) {
            const oldGetLabelLayout = this.getLabelLayout;
            this.getLabelLayout = (object, ctx) => {
                if (!selector || selector(object, ctx)) {
                    return labelLayout;
                }
                return oldGetLabelLayout && oldGetLabelLayout(object, ctx);
            };
        }
    }
};

Gantt.components.ConstraintsGraph.impl = ConstraintsGraph;



